<template>
  <div class="setting-content">
    <h1>{{ $t("settings") }}</h1>
    <h3 v-if="!settingsExist && !setingIsSaved">
      {{ $t("selectAndSave") }}
    </h3>
    <h3 v-if="setingIsSaved">{{ $t("savedMessage") }}</h3>
    <div v-if="!loading">
      <div class="form-model" v-if="!userSetting.location">
        <label for="location">{{ $t("location") }}:</label>
        <input
          type="text"
          id="location"
          v-model="searchText"
          @input="searchLocation"
          placeholder="Enter a city"
        />
        <p v-if="results.length > 0">{{ $t("chooseLocation") }}</p>
        <ul class="results-list" v-if="results.length > 0">
          <li
            class="result-item"
            v-for="r in results"
            :key="r.place_id"
            @click="setLocation(r)"
          >
            {{ r.formatted_address }}
          </li>
        </ul>
      </div>
      <div class="location-text" v-else>
        <p>{{ userSetting.location.formatted_address }}</p>
        <button class="edit-btn" @click="del('location')">
          {{ $t("changeLocation") }}
        </button>
      </div>
      <div class="form-model" v-if="!userSetting.method">
        <label for="calculation-method">{{ $t("calculationMethod") }}:</label>
        <select
          id="calculation-method"
          v-model="calculationMethod"
          @change="setMethod"
        >
          <option v-for="m in methods" :value="m" :key="m.key">
            {{ $t(m.key) }}
          </option>

          <!-- add more options as needed -->
        </select>
      </div>
      <div class="method-text" v-else>
        <p>{{ $t(userSetting.method.key) }}</p>
        <button class="edit-btn" @click="del('method')">
          {{ $t("changeMethod") }}
        </button>
      </div>
      <!-- <div class="form-model" v-if="!userSetting.timeZone">
        <label for="time-zone">Time Zone:</label>
        <select id="time-zone" @change="setTimeZone" v-model="timeZone">
          <option v-for="tz in timeZones" :value="tz" :key="tz.id">
            {{ tz.label }}
          </option>
        </select>
      </div>
      <div class="timezone-text" v-else>
        <p>{{ userSetting.timeZone.label }}</p>
        <button class="edit-btn" @click="del('timeZone')">
          Change TimeZone
        </button>
      </div> -->
    </div>
    <PulseLoader v-else />
  </div>
</template>
<script>
import axios from "axios";
import PulseLoader from "vue-spinner/src/PulseLoader.vue";

export default {
  components: {
    PulseLoader,
  },
  data() {
    return {
      calculationMethod: "",
      timeZone: "",
      searchText: "",
      results: [],
      userSetting: {
        location: undefined,
        method: undefined,
      },
      methods: [],
      loading: false,
    };
  },
  computed: {
    settingsExist() {
      return (
        this.userSetting.location !== undefined &&
        this.userSetting.method !== undefined
      );
    },
    setingIsSaved() {
      const savedSetting = localStorage.getItem("prayer-times-user-settings");
      if (savedSetting !== null) {
        const localUserSetting = JSON.parse(savedSetting);
        return (
          JSON.stringify(localUserSetting) === JSON.stringify(this.userSetting)
        );
      } else return false;
    },
  },
  mounted() {
    const savedSetting = localStorage.getItem("prayer-times-user-settings");
    if (savedSetting !== null) {
      this.userSetting = JSON.parse(savedSetting);
    }
    this.methods = this.prayTimes.getAllMethods();
  },

  methods: {
    searchLocation() {
      if (!this.searchText) return;
      axios
        .get("https://maps.googleapis.com/maps/api/geocode/json", {
          params: {
            address: this.searchText,
            key: "AIzaSyDPG9AxZJVYewEOt0xIxeGvZoxYIurcij0",
          },
        })
        .then((response) => {
          if (response.data.results.length > 0) {
            this.results = response.data.results;
          } else {
            console.log("no result");
          }
        })
        .catch((error) => {
          console.error("Error:", error.message);
        });
    },
    setLocation(r) {
      this.userSetting = { ...this.userSetting, location: r };
      localStorage.setItem(
        "prayer-times-user-settings",
        JSON.stringify(this.userSetting)
      );
      this.results = [];
      this.searchText = "";
    },

    setMethod() {
      this.userSetting = {
        ...this.userSetting,
        method: this.calculationMethod,
      };
      localStorage.setItem(
        "prayer-times-user-settings",
        JSON.stringify(this.userSetting)
      );
    },
    // setTimeZone() {
    //   this.userSetting = {
    //     ...this.userSetting,
    //     timeZone: this.timeZone,
    //   };
    //   localStorage.setItem(
    //     "prayer-times-user-settings",
    //     JSON.stringify(this.userSetting)
    //   );
    // },

    del(key) {
      this.userSetting = {
        ...this.userSetting,
        [key]: undefined,
      };
      if (key == "location") {
        this.results = [];
        this.searchText = "";
      }
    },
  },
  watch: {
    // eslint-disable-next-line no-unused-vars
    searchText(newValue, oldValue) {
      if (!newValue || newValue == "") {
        this.results = [];
      }
    },
  },
};
</script>
