    .result-item {
      padding: 5px;
      margin-bottom: 10px;
      border: 1px solid #efefef;
      cursor: pointer;
      &:hover {
        border: 1px solid #336699;
      }
    }
  }
  .save {
    .alert-text {
      color: rgb(253, 6, 6);
